import { Component } from '@angular/core';
import { ASUNTO_MENSAJE_RENDICION_CUENTAS } from 'src/app/commons/asunto-mensaje';
import { Mensaje } from 'src/app/entities/mensaje';

@Component({
  selector: 'app-redicion-cuentas',
  templateUrl: './redicion-cuentas.page.html',
  styleUrls: ['./redicion-cuentas.page.scss'],
})
export class RedicionCuentasPage {

  opcion: string;

  filterRendicionCuentas(mensaje: Mensaje): boolean {
    return mensaje.asunto === ASUNTO_MENSAJE_RENDICION_CUENTAS;
  }

  ionViewWillEnter(): void {
    this.opcion = 'accountability';
  }

  ionViewDidLeave(): void {
    this.opcion = undefined;
  }

}
